#about {
  margin: 0 5vw 0 5vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: justify;
}

#about img {
  margin-bottom: 2.5vh;
  height: 35vh;
  width: 35vh;
}

#about h3 {
  color: #ED6112;
  margin: 0;
}

.light-about-text {
  color: #01002E;
}

.dark-about-text {
  color: white;
}
